<template>
  <div class="ratings" ref="ratingscroll">
  	 <div class="ratings-content">
  	 	<div class="rating-top">
  	 		<div class="left">
	          <h1 class="score">{{seller.score}}</h1>
	          <div class="title">综合评分</div>
	          <div class="rank">高于周边商家{{seller.rankRate}}%</div>
	        </div>
	        <div class="right">
	          <div class="score-wrapper">
	            <span class="title">服务态度</span>
	            <v-star :size="36" :score="seller.serviceScore"></v-star>
	            <span class="score">{{seller.serviceScore}}</span>
	          </div>
	          <div class="score-wrapper">
	            <span class="title">商品评分</span>
	            <v-star :size="36" :score="seller.foodScore"></v-star>
	            <span class="score">{{seller.foodScore}}</span>
	          </div>
	          <div class="delivery-wrapper">
	            <span class="title">送达时间</span>
	            <span class="delivery">{{seller.deliveryTime}}分钟</span>
	          </div>
	        </div>
  	 	</div>
  	 	<v-split></v-split>
  	 	<v-ratingselect @select="selectRating" @toggle="toggleContent" :selectType="selectType" :onlyContent="onlyContent" :desc="desc" :ratings="ratings"></v-ratingselect>
  	 	<div class="rating-wrapper">
	        <ul>
	          <li v-for="rating in ratings" v-show="needShow(rating.rateType, rating.text)" class="rating-item border-1px">
	            <div class="avatar">
	              <img width="28" height="28" :src="rating.avatar">
	            </div>
	            <div class="content">
	              <h1 class="name">{{rating.username}}</h1>
	              <div class="star-wrapper">
	                <v-star :size="24" :score="rating.score"></v-star>
	                <span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}</span>
	              </div>
	              <p class="text">{{rating.text}}</p>
	              <div class="recommend" v-show="rating.recommend && rating.recommend.length">
	                <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>
	                <span class="item" v-for="item in rating.recommend">{{item}}</span>
	              </div>
	              <div class="time">
	                {{rating.rateTime | formatDate}}
	              </div>
	            </div>
	          </li>
	        </ul>
	      </div>
  	 </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'; //https://ustbhuangyi.github.io/better-scroll/doc/zh-hans/installation.html#npm
import shopcart from 'components/shopcart/shopcart.vue' //购物车组件
import split from 'components/split/split.vue'; //分割线组件
import {formatDate} from 'common/js/date';
import star from 'components/star/star';
import ratingselect from 'components/ratingselect/ratingselect';

const ALL = 2;
const ERR_OK = 0;
const debug = process.env.NODE_ENV !== 'production';
export default {
  data (){
    return{
      ratings: [],
	  selectType: ALL,
	  onlyContent: true,
	  desc:{
          all: '全部',
          positive: '满意',
          negative: '不满意'
        }
    }
  },
  props:{
  	//获取父元素传过来的值
  	seller: {
  	  type: Object
  	}
  },
  created(){
    const url = debug ? '/api/ratings' : 'http://ustbhuangyi.com/sell/api/ratings';
    this.$http.get('/api/ratings', {"id": this.seller.id}).then( (response) => {
        response = response.body
        if (response.errno === ERR_OK) {
          this.ratings = response.data;
          this.$nextTick(() => {
            this.scroll = new BScroll(this.$refs.ratingscroll, {
	            click: true
	          })  	
          })
          console.log(this.seller.id)
	    }
    })
  },
  methods:{
    needShow(type, text) {
        if (this.onlyContent && !text) {
          return false;
        }
        if (this.selectType === ALL) {
          return true;
        } else {
          return type === this.selectType;
        }
      },
      selectRating(type) {
        this.selectType = type;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      toggleContent() {
        this.onlyContent = !this.onlyContent;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      }
  },
  filters: {
      formatDate(time) {
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd hh:mm');
      }
   },
  components:{
     'v-shopcart' : shopcart,
     'v-split' : split,
     'v-ratingselect' : ratingselect,
     'v-star' : star
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @import '../../common/stylus/mixin.styl'
    .ratings
   	   position:absolute
   	   top:174px
   	   bottom: 0px
   	   width:100%
   	   overflow:hidden
   	   .rating-top
   	   	  display: flex
   	   	  padding: 18px 0
	      .left
	        flex: 0 0 137px
	        padding: 6px 0
	        width: 137px
	        border-right: 1px solid rgba(7, 17, 27, 0.1)
	        text-align: center
	        @media only screen and (max-width: 320px)
	          flex: 0 0 120px
	          width: 120px
	        .score
	          margin-bottom: 6px
	          line-height: 28px
	          font-size: 24px
	          color: rgb(255, 153, 0)
	        .title
	          margin-bottom: 8px
	          line-height: 12px
	          font-size: 12px
	          color: rgb(7, 17, 27)
	        .rank
	          line-height: 10px
	          font-size: 10px
	          color: rgb(147, 153, 159)
	      .right
	        flex: 1
	        padding: 6px 0 6px 24px
	        @media only screen and (max-width: 320px)
	          padding-left: 6px
	        .score-wrapper
	          margin-bottom: 8px
	          font-size: 0
	          .title
	            display: inline-block
	            line-height: 18px
	            vertical-align: top
	            font-size: 12px
	            color: rgb(7, 17, 27)
	          .star
	            display: inline-block
	            margin: 0 12px
	            vertical-align: top
	          .score
	            display: inline-block
	            line-height: 18px
	            vertical-align: top
	            font-size: 12px
	            color: rgb(255, 153, 0)
	        .delivery-wrapper
	            font-size: 0
	            .title
	              line-height: 18px
	              font-size: 12px
	              color: rgb(7, 17, 27)
		        .delivery
		           margin-left: 12px
		           font-size: 12px
		           color: rgb(147, 153, 159)
	   .rating-wrapper
	      padding: 0 18px
	      .rating-item
	        display: flex
	        padding: 18px 0
	        border-1px-bottom(rgba(7, 17, 27, 0.1))
	        .avatar
	          flex: 0 0 28px
	          width: 28px
	          margin-right: 12px
	          img
	            border-radius: 50%
	        .content
	          position: relative
	          flex: 1
	          .name
	            margin-bottom: 4px
	            line-height: 12px
	            font-size: 10px
	            color: rgb(7, 17, 27)
	          .star-wrapper
	            margin-bottom: 6px
	            font-size: 0
	            .star
	              display: inline-block
	              margin-right: 6px
	              vertical-align: top
	            .delivery
	              display: inline-block
	              vertical-align: top
	              line-height: 12px
	              font-size: 10px
	              color: rgb(147, 153, 159)
	          .text
	            line-height: 18px
	            color: rgb(7, 17, 27)
	            font-size: 12px
	          .recommend
	            line-height: 16px
	            font-size: 0
	            margin-top: 8px
	            .icon-thumb_up, .item, .icon-thumb_down
	              display: inline-block
	              margin: 0 8px 4px 0
	              font-size: 9px
	            .icon-thumb_up
	              color: rgb(0, 160, 220)
	            .icon-thumb_down
	              color: rgb(147, 153, 159)
	            .item
	              padding: 0 6px
	              border: 1px solid rgba(7, 17, 27, 0.1)
	              border-radius: 1px
	              color: rgb(147, 153, 159)
	              background: #fff
	          .time
	            position: absolute
	            top: 0
	            right: 0
	            line-height: 12px
	            font-size: 10px
	            color: rgb(147, 153, 159)
   	   
</style>


