<template>
  <div class="seller-wrapper" ref="sellerscroll">
     <div class="seller-content">
     	<div class="seller-top">
     		<h1 class="title">{{seller.name}}</h1>
     		<div class="desc border-1px">
     			<v-star :size="36" :score="seller.score"></v-star>
     			<span class="text">({{seller.ratingCount}})</span>
     			<span class="text">月销{{seller.sellCount}}单</span>
     		</div>
     		<ul class="remark">
		        <li class="block">
		            <h2>起送价</h2>
		            <div class="content">
		              <span class="stress">{{seller.minPrice}}</span>元
		            </div>
		        </li>
		        <li class="block">
		            <h2>商家配送</h2>
		            <div class="content">
		              <span class="stress">{{seller.deliveryPrice}}</span>元
		            </div>
		        </li>
		        <li class="block">
		            <h2>平均配送时间</h2>
		            <div class="content">
		              <span class="stress">{{seller.deliveryTime}}</span>分钟
		         </div>
	          </li>
	        </ul>
	        <div class="favorite" @click="toggleFavorite">
	          <span class="icon-favorite" :class="{'active':favorite}"></span>
	          <span class="text">{{favoriteText}}</span>
	        </div>
     	</div>
     	<v-split></v-split>
     	<div class="bulletin">
            <h1 class="title">公告与活动</h1>
            <p class="content-text border-1px">{{seller.bulletin}}</p>
            <ul class="bulletin-list" v-if="seller.supports">
            	<li v-for="(item,index) in seller.supports" class="item border-1px">
            		<span class="icon" v-bind:class="classMap[index]"></span>
            		<span class="text">{{item.description}}</span>
            	</li>
            </ul>
	    </div>
        <v-split></v-split>
        <div class="pics">
	        <h1 class="title">商家实景</h1>
	        <div class="pic-wrapper" ref="picWrapper">
	          <ul class="pic-list" ref="picList">
	            <li class="pic-item" v-for="pic in seller.pics">
	              <img :src="pic" width="120" height="90">
	            </li>
	          </ul>
	        </div>
	    </div>
	    <v-split></v-split>
	    <div class="info">
	        <h1 class="title border-1px">商家信息</h1>
	        <ul>
	          <li class="info-item border-1px" v-for="info in seller.infos">{{info}}</li>
	        </ul>
	      </div>
     </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'; //https://ustbhuangyi.github.io/better-scroll/doc/zh-hans/installation.html#npm
import split from 'components/split/split.vue'; //分割线组件
import {saveToLocal, loadFromLocal} from 'common/js/store'; //收藏插件
import star from 'components/star/star';
export default {
  data (){
    return{
      classMap: ['decrease','discount','guarantee','invoice','special'],
      favorite: (() => {
          return loadFromLocal(this.seller.id, 'favorite' , false);
      })()
    }
  },
  props:{
    seller: {
      type:Object
    }
  },
  watch: {
      'seller'() {
        this.$nextTick(() => {
          this._initScroll();
          this._initPics();
        });
      }
  },
  created() {
    
  },
  mounted() {
     this.$nextTick(()=> {
    	this._initScroll();
    	this._initPics();
    })
  },
 computed:{
   favoriteText (){
   	return this.favorite ? '已收藏': '收藏' ;
   }
 },
  methods:{
  	toggleFavorite(event) {
        if (!event._constructed) {
          return;
        }
        this.favorite = !this.favorite;
        saveToLocal(this.seller.id, 'favorite', this.favorite); //存入
        //var shoucan=loadFromLocal(this.seller.id, 'favorite' , 'none'); //读取
        //console.log(shoucan)
    },
  	_initScroll() {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.sellerscroll, {
            click: true
          });
        } else {
          this.scroll.refresh();
        }
      },
    _initPics() {
        if (this.seller.pics) {
          let picWidth = 120;
          let margin = 6;
          let width = (picWidth + margin) * this.seller.pics.length - margin;
          this.$refs.picList.style.width = width + 'px';
          this.$nextTick(() => {
            if (!this.picScroll) {
              this.picScroll = new BScroll(this.$refs.picWrapper, {
                scrollX: true,
                eventPassthrough: 'vertical'
              });
            } else {
              this.picScroll.refresh();
            }
          });
        }
      }
  },
  components:{
    'v-star' : star,
    'v-split': split
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @import '../../common/stylus/mixin.styl'
    .seller-wrapper
   	   position:absolute
   	   top:174px
   	   bottom: 0px
   	   width:100%
   	   overflow:hidden
   	   .seller-top
   	      position: relative
   	      padding: 18px
	      .title
	        margin-bottom: 8px
	        line-height: 14px
	        color: rgb(7, 17, 27)
	        font-size: 14px
	        font-weight:700
	      .desc
	        padding-bottom: 18px
	        border-1px-bottom(rgba(7, 17, 27, 0.1))
	        font-size: 0
	        .star
	          display: inline-block
	          margin-right: 8px
	          vertical-align: top
	        .text
	          display: inline-block
	          margin-right: 12px
	          line-height: 18px
	          vertical-align: top
	          font-size: 10px
	          color: rgb(77, 85, 93)
	      .remark
	        display: flex
	        padding-top: 18px
	        .block
	          flex: 1
	          text-align: center
	          border-right: 1px solid rgba(7, 17, 27, 0.1)
	          &:last-child
	            border: none
	          h2
	            margin-bottom: 4px
	            line-height: 10px
	            font-size: 10px
	            color: rgb(147, 153, 159)
	          .content
	            line-height: 24px
	            font-size: 10px
	            color: rgb(7, 17, 27)
	            .stress
	              font-size: 24px
	      .favorite
	        position: absolute
	        width: 50px
	        right: 11px
	        top: 18px
	        text-align: center
	        .icon-favorite
	          display: block
	          margin-bottom: 4px
	          line-height: 24px
	          font-size: 24px
	          color: #d4d6d9
	          &.active
	            color: rgb(240, 20, 20)
	        .text
	          line-height: 10px
	          font-size: 10px
	          color: rgb(77, 85, 93)
	   .bulletin
         padding:18px 18px 0px 18px 
         .title
         	margin-bottom: 8px
         	line-height: 14px
         	color: rgb(7, 17, 27)
         	font-size: 14px
         	font-weight:700
         .content-text
          	font-size:12px
          	color:rgb(240,20,20) 
          	line-height:24px
          	padding:0px 12px 16px 12px
          	border-1px-bottom(rgba(7,17,27,0.1))
         .bulletin-list
         	.item
         	  padding:16px 12px
         	  font-size:0
         	  border-1px-bottom(rgba(7,17,27,0.1))
         	  &:last-child
         	  	border-none()
         	  .icon
         	  	display: inline-block
         	  	vertical-align: middle
         	  	width: 16px
         	  	height: 16px
         	  	margin-right: 6px
         	  	background-size: 100% auto
         	  	background-repeat: no-repeat
         	  	&.decrease
         	  	   bg-image('decrease_4')
         	  	&.discount
         	  	   bg-image('discount_4')
         	  	&.guarantee
         	  	   bg-image('guarantee_4')
         	  	&.invoice
         	  	   bg-image('invoice_4')
         	  	&.special
         	  	   bg-image('special_4')
         	  .text
         	  	display: inline-block
         	  	vertical-align: middle
         	  	font-size: 12px
         	  	color: rgb(7,17,27)
         	  	line-height: 16px  	  
	   .pics
	      padding: 18px
	      .title
	        margin-bottom: 12px
	        line-height: 14px
	        color: rgb(7, 17, 27)
	        font-size: 14px
	      .pic-wrapper
	        width: 100%
	        overflow: hidden
	        white-space: nowrap
	        .pic-list
	          font-size: 0
	          .pic-item
	            display: inline-block
	            margin-right: 6px
	            width: 120px
	            height: 90px
	            &:last-child
	              margin: 0
	   .info
	      padding: 18px 18px 0 18px
	      color: rgb(7, 17, 27)
	      .title
	        margin-bottom: 12px
	        line-height: 14px
	        font-size: 14px
	      .info-item
	        padding: 16px 12px
	        line-height: 16px
	        border-1px-top(rgba(7, 17, 27, 0.1))
	        font-size: 12px
</style>


